@use "variables";

.event {
  padding: 80px 0;
  background-image: linear-gradient(#1b6ca7, #442bdb);

  &--frontend {
    background-image: linear-gradient(variables.$blue1, variables.$blue2);
  }

  &--ux-ui {
    background-image: linear-gradient(variables.$blue2, variables.$pink);
  }

  &--data-science {
    background-image: linear-gradient(variables.$pink, variables.$yellow);
  }

  &--backend {
    background-image: linear-gradient(variables.$yellow, variables.$pink);
  }

  &--image-left {
    .container {
      flex-direction: row-reverse;
    }
  }

  .container {
    display: flex;
    gap: 40px;
  }

  &__image {
    max-width: 360px;
  }

  &__details {
    &__title {
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 16px;
    }

    &__description {
      line-height: 22px;
    }
  }

  @media (max-width: 640px) {
    .container {
      display: block;
    }

    &__image {
      width: 100%;
    }

    &__details {
      margin-right: 0;
      margin-bottom: 16px;
    }
  }
}
